<template>
  <div class="forgotpassword">
    <div class="Form_Box">
      <div>
        <Logo />
      </div>
      <div class="Border_Top_Box forgot_box">
        <h4 class="h_text">Forgot Password</h4>
        <p class="text">We will send a link to reset your password</p>
        <el-form label-position="top" ref="forgotForm" :rules="rules" label-width="80px" :model="form">
          <el-form-item label="Email" prop="email">
            <el-input v-model="form.email"></el-input>
          </el-form-item>
        </el-form>
        <div>
          <el-button type="primary" @click="handleSubmit('forgotForm')" class="Form_Submit" size="nomal">Forgot Password</el-button>
        </div>
      </div>
      <Footer />
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      form: {
        email: ''
      },
      rules: {
        email: [
          { required: true, message: '请输入Email', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleSubmit (form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import url('../../assets/css/var.less');
.forgotpassword {
  background: @reg_bg;
  min-height: 100vh;
  .h_text {
    color: @color;
  }
  .text {
    color: #98a6ad;
    margin: 50px 0 30px;
  }
}
</style>